* {
  margin: 0;
  padding: 0;
  list-style: none;
  vertical-align: baseline;
}

.App {
  display: flex;
  align-items: center;
  height: 100vh;
  justify-content: center;
  background: linear-gradient(to bottom right, #3d4258, #6670a0, #9688b4);
  font-family: "Nunito", sans-serif;
}

blockquote {
  font-size: 2.5rem;
  margin: 0.8em 0.8em 0.8em 0;
  opacity: 0.8;
}

#quote-box {
  display: flex;
  height: auto;
  max-width: 60vw;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background: linear-gradient(to bottom right, white, #f8f8f8);
  box-shadow: 5px 15px 20px -15px rgba(0, 0, 0, 0.3),
    0 35px 50px -25px rgba(0, 0, 0, 0.3), 0 85px 60px -25px rgba(0, 0, 0, 0.1);
}
#new-quote {
  border: 1px solid transparent;
  border-radius: 40px;
  text-decoration: none;
  padding: 0.5rem 1rem;
  margin: 1em;
  color: rgba(255, 255, 255, 0.7);
  font-weight: 700;
  background: rgba(10, 20, 30, 0.1);
  font-size: 2rem;
  outline: none;
}
#new-quote:hover {
  background: rgba(10, 20, 30, 0.3);
}
#new-quote:active {
  transform: scale(0.99);
}

#buttons {
  display: flex;
  flex-direction: row;
  background-color: ;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  background-color: #edecf0;
}
#tweet-quote {
  margin: 1.4em;
  padding-left: 0.6em;
  outline: none;
}

.loader {
  border: 16px solid #f3f3f3;
  border-top: 16px solid #3498db;
  border-radius: 50%;
  width: 120px;
  height: 120px;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@media only screen and (max-width: 768px) {
  #quote-box {
    max-width: 95vw;
  }
  blockquote {
    font-size: 1.7rem;
  }
  #new-quote {
    font-size: 1.7rem;
  }
}
